import React from 'react';
import PropTypes from 'prop-types';
import YearCarousel from '../layout/yearCarousel';
import ArrayHandler from '../../utils/array-handler';
import '../../styles/animate.min.css';
import _ from 'lodash';
import {Container, Row, Col} from 'reactstrap';
import Time from '../layout/time'
import T from '../layout/translation';

export default class FinancialPublication extends React.Component {
    constructor() {
        super();
        this.state = {
            currentYear: 0
        }
    }

    static propTypes = {
        items: PropTypes.object.isRequired,
        lang: PropTypes.string.isRequired
    };

    selectYear = (year) => {
        this.setState({
            currentYear: year
        });
    };

    render() {
        let years = _.keys(this.props.items).sort().reverse();
        return (
            <Container>
                <Row>
                    <Col xs="12">
                        <YearCarousel years={years} clickCallback={this.selectYear}/>
                    </Col>
                </Row>
                <Row>
                    <Col lg={{ size: 6, offset: 3 }}  md={{ size: 8, offset: 2 }} xs="12" className="financial-publication">
                        {years.map(year => (
                            <div key={year}>
                                {this.state.currentYear === year || this.state.currentYear === 0 ? (
                                    <div className={'animated fast bounceInUp'}>
                                        <ItemPeriod lang={this.props.lang} items={this.props.items[year]}/>
                                    </div>
                                ) : null}
                            </div>
                        ))}
                    </Col>
                </Row>
            </Container>
        )
    }
}

class ItemPeriod extends React.Component {

    static propTypes = {
        items: PropTypes.object.isRequired,
        lang: PropTypes.string.isRequired
    };

    render() {
        let periods = Object.keys(this.props.items);
        return (
            <>
                {periods.map(period => (
                    <div key={period} className="financial-publication-container">
                        <h4>{period}</h4>
                        <div>
                            <div>
                                {ArrayHandler.sortColumns(this.props.items[period], ['date', 'title'], ['desc', 'desc']).map(node => (
                                    <Row key={node.nid} className="financial-publication-item">
                                        <Time lang={this.props.lang} date={node.date}/>
                                        <Col lg="9"  md="8" xs="8">
                                            <h5>{node.title}</h5>
                                            <a href={node.file} target="_blank">
                                                <T message="Download"/>
                                            </a>
                                        </Col>
                                    </Row>
                                ))}
                            </div>
                        </div>
                    </div>
                ))}
            </>
        );
    }
}
