import React from 'react'

import Layout from '../components/layout/layout'
import PageContent from '../components/layout/pageContent';
import FinancialPublication from '../components/content/financialPublicationComponent';
import items_en from '../data/api/data--financial_publication_component--en';
import items_fr from '../data/api/data--financial_publication_component--fr';

const FinancialPublicationPage = ({pageContext}) => {
    let items = pageContext.lang === 'en' ? items_en : items_fr;
    return (
        <Layout context={pageContext}>
            <PageContent context={pageContext}/>
            <FinancialPublication lang={pageContext.lang} items={items}/>
        </Layout>
    )
};

export default FinancialPublicationPage
